<template>
  <div class="user-table-select">
    <el-dialog
        title="添加管理员"
        :visible.sync="visible"
        :before-close="doCancel">
      <div class="qrcode-bar">
        <div class="-inner">
          <div class="-login">
            <a class="-image"><img :src="this.qrcode"/></a>
            <div class="-desc">微信扫一扫，成为管理员</div>
          </div>
        </div>
      </div>
    </el-dialog>
  </div>
</template>

<script>
  import UserApi from "@/api/manage/user";

  export default {
    name: "add-user",
    data() {
      return {
        qrcode: '',
        token: '',
        timer: null,
      }
    },
    props: {
      visible: {
        type: Boolean,
        default: false
      }
    },
    watch: {
      visible: function () {
        if (this.visible) {
          this.loadQrCode()
        }
      }
    },
    methods: {
      loadQrCode: function () {
        UserApi.qrcode().then(res => {
          this.token = res.obj.token;
          this.qrcode = res.obj.qrcode;
          this.touchTask();
        });
      },
      touchTask: function () {
        if (this.timer != null) {
          clearTimeout(this.timer);
        }
        this.timer = setTimeout(() => {
          this.doTouch();
        }, 1000);
      },
      doTouch: function () {
        UserApi.touch(this.token).then(res => {
          if (!res.success) {
            this.touchTask();
          }
        })
      },
      doCancel: function () {
        this.$emit('update:visible', false)
        if (this.timer != null) {
          clearTimeout(this.timer);
        }
      },
    },
    destroyed: function () {
      if (this.timer != null) {
        clearTimeout(this.timer);
      }
    }
  }
</script>

<style lang="stylus" rel="stylesheet/stylus" scoped>
  @require "../../common/stylus/variable.styl"
  .qrcode-bar {
    text-align center;
  }
</style>