<template>
  <div class="tax-page manager">
    <h4>帐户管理员</h4>
    <div class="content">
      <div class="options">
        <span>绑定微信用户之后，微信用户可以登录后台、报表管理等单位管理操作</span>
        <div class="btns">
          <el-button type="primary" @click="addManager">添加管理员</el-button>
        </div>
      </div>
      <table class="table sample-table table-bordered table-hover">
        <thead>
        <tr>
          <th scope="col" width="220">微信</th>
          <th scope="col" width="120">状态</th>
          <th scope="col">操作</th>
        </tr>
        </thead>
        <tbody>
        <tr v-for="user in managers" :key="user.id">
          <td><span>{{user.headimg}}</span><span>{{user.name}}</span></td>
          <td>{{user.createDate}}绑定</td>
          <td><a class="del" @click.stop="delManager" :data-id="user.id">解除绑定</a></td>
        </tr>
        </tbody>
      </table>
    </div>
    <add-user :visible.sync="addManagerDialog.visible"></add-user>
  </div>
</template>

<script>
  import UserApi from '@/api/manage/user';
  import ElButton from "element-ui/packages/button/src/button";
  import AddUser from "../../ui/components/user/addUser";

  export default {
    components: {
      AddUser,
      ElButton
    },
    name: "manager",
    data() {
      return {
        managers: [],
        addManagerDialog: {
          visible: false
        }
      }
    },
    mounted() {
      this.$emit('layout:load', "manager")
      this.load()
    },
    methods: {
      load: function () {
        UserApi.managers().then(res => {
          this.managers = res.obj;
        })
      },
      addManager: function () {
        this.addManagerDialog.visible = true;
      },
      delManager: function (e) {
        var id = e.target.getAttribute("data-id");
        this.$confirm("确认删除此管理员，删除之后此用户不能登录后台，如果切换了管理员请重置AppSecret", '确认删除？', {
          confirmButtonText: '确定',
          cancelButtonText: '取消',
          type: 'warning'
        }).then(() => {
          UserApi.delete(id).then(res => {
            this.load()
            this.$notify({
              title: '成功',
              message: '删除成功!',
              type: 'success'
            });
          })
        }).catch(() => {

        })
      }
    }
  }
</script>

<style lang="stylus" rel="stylesheet/stylus" scoped>
  @require "../../ui/common/stylus/variable.styl"
  .del {
    color $color-info;
    cursor pointer;
  }
</style>